import React, { useState, useEffect} from 'react';
import {BrowserRouter as Router, useHistory, withRouter} from 'react-router-dom';
import {Button, Modal, ModalHeader, ModalBody, ModalFooter,Form, FormGroup, Label, Input} from 'reactstrap';
import axios from 'axios';
import { apiUri, adminRoot} from '../../constants/defaultValues';
import IntlMessages from '../../helpers/IntlMessages';

const Popups = (props) => {
  const {
    className, actionSet
  } = props;
  
  const [hackData, SetChallengeList] = useState([])
  useEffect(() => {
  }, []);

  const [modal, setModal] = useState(false);
  const [hackName, setHackName] = useState(null);
  const [hackType, setHackType] = useState(null);
  const [next, setNext] = useState(false);
  const [message, setMessage] = useState(null)
  const addHackathon = () => {
    const userId = JSON.parse(window.localStorage.getItem('sn_user'));
    console.log('user Id', userId)
    const data = {
      name: hackName,
      organizer_id: userId.id,
      hackathon_type_id: hackType
    }
    
    axios.post(apiUri+'/createHackathon', data).then((response) => {
      const dataItem = response.data;
      if(dataItem.success === true) {
        window.localStorage.setItem('current_hack_id', dataItem.details.id)
        setNext(true)
        props.actionSet()
      }else{
        setMessage(dataItem.message)
      }
      // handleHistory()
      // setModal(false)
    });
  }
  const toggle = () => setModal(!modal);
  const hackTypeChange =(e) => {
    setHackType(e.target.value);
    
  }

  const onChangeHandler = (e) => {
    var filter = e.target.value;
    setHackName(filter)
  };

  const history=useHistory();
    const handleHistory=()=>{
        history.push("/home/Challenges/add-challenges");
    }
  console.log(hackType, hackName)
  return (
    <div>
           
      <Button color="primary" onClick={toggle}>Organize Hackathon</Button>
      <Modal isOpen={modal} toggle={toggle} className={className}>
        <ModalHeader toggle={toggle}>
            <h6>Great Decision</h6>
            <h1>Lets get you started</h1>
        </ModalHeader>
        <ModalBody>
        <Form>
      <FormGroup>
        <Label for="OrganizerName">Name</Label>
        <Input type="text" name="Name" id="organizerName" placeholder="Enter Your Name" onChange={(text) => {onChangeHandler(text); setMessage(null)}} />
            {message != null && message}
      </FormGroup>

      <FormGroup>
        <Label for="Hackathon">Hackathon</Label>
        {/* <Input type="select" name="Hackathon" id="Hackathon">
          <option>Online</option>
          <option>Offline</option>
         
        </Input> */}
        <select 
            value={hackType} 
            onChange={hackTypeChange} 
          style={{width: '100%', height: '35px'}}>
          <option value="0">Select Hackathon Type</option>
          <option value="1">Online</option>
            <option value="2">Blenders</option>
            <option value="3">Offline</option>
          </select>
          
      </FormGroup>
      </Form>
          </ModalBody>
        <ModalFooter>
          {/* <Button  color="primary" onClick={addHackathon}>Begin</Button> */}
         {next ? 
            <Router>
         <Button
              color="primary"
              className="m-3"
              onClick={addHackathon}
              href={`${adminRoot}/Challenges/add-challenges`}
            >
              <IntlMessages id="forms.next" />
            </Button>
            </Router>
            :
            <Router>
            <Button
              color="primary"
              className="m-3"
              onClick={addHackathon}
            >
              <IntlMessages id="forms.begin" />
            </Button>
          <Button color="secondary" onClick={toggle}>Cancel</Button> </Router>}
        </ModalFooter>
      </Modal>
     
    </div>
  );
}

export default withRouter(Popups);